.header{
    background-color: #8a8383;
  height: 9rem;
  display: flex;
  justify-content: center;
  align-items: center;


    .header_cont{
        align-items: center;
  display: flex;
  justify-content: space-between;
  gap: 4rem;
        .navegacion{
            display: flex;
            align-items: center;
            gap: 2rem;
            a{
                display: inline-block;
                color: $blanco;
                font-size: 1.3rem;
                padding: 1rem;
            }
            a:hover{
                padding: 1rem;
                background-color: $grisClaro2;
                color: $rosado;
                border-radius: 1rem;
                -webkit-transition: ease-out 0.3s;
                -moz-transition: ease-out 0.3s;
                transition: ease-out 0.3s;
            }
        }
        .redes{
            display: flex;
            gap: 2rem;
            .redes_div{
                background-color: $blanco;
                display: inline-flex;
                justify-content: center;
                align-items: center;
                border-radius: 50%;
                width: 2.5rem;
                height: 2.5rem;
                a{
                    i{
                        color: $rosado;
                    }
                }
            }
        }
    }
}

