// imagen del header
*{
	margin: 0;
	padding: 0;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}


.slideshow{
    width: 100%;
    height:60rem;
	position: relative;
    overflow: hidden;
	ul,ol{
		list-style: none;
		margin: 0;
	}
}
.slideshow2{
    width: 100%;
    height:40rem;
	position: relative;
    overflow: hidden;
	ul,ol{
		list-style: none;
		margin: 0;
	}
}
.slideshow3{
    width: 100%;
    height:100%;
	position: relative;
    overflow: hidden;
	ul,ol{
		list-style: none;
		margin: 0;
	}
}
.slider li, ul,
.slider2 li, ul,
.slider3 li, ul{
	width: 100%;
}

.slider li,
.slider2 li,
.slider3 li{
	overflow: hidden;
}

.slider li .img_slider
{
    width: 100%;
    height:60rem;
    overflow: hidden;
    animation-duration: 4s;
	animation-name: slidein;
    object-fit: cover;
}
.slider2 li .img_slider{
	width: 100%;
    height:40rem;
	overflow: hidden;
	animation-duration: 4s;
    border-radius: 3px;
}
.slider3 li .img_slider{
	width: 100%;
    height:40rem;
	overflow: hidden;
	animation-duration: 4s;
    border-radius: 3px;
}
@keyframes slidein {
from {
  transform: scale(1);
}

to {
    transform: scale(1.2);
    
}
}
.slider .caption{
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	padding: 15px 50px;

	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;

	text-align: center;
	color: #fff;
	z-index: 1;
}

.slider .caption h1{
	font-size: 50px;
}

.slider .caption p{
	margin-top: 10px;
	font-size: 20px;
}

.pagination{
	position: absolute;
	top: 102%;
	width: 100%;

	display: flex;
	justify-content: center;
}

.pagination li{
	font-size: 20px;
	margin: 2px 5px;
	color: #909196;
	cursor: pointer;
}

.left, .right{
	position: absolute;
	top: 0;
	height: 100%;

	display: flex;
	align-items: center;

	color: #fff;
	font-size: 35px;
	cursor: pointer;
	z-index: 2;
}

.left{
	left: 10px;
}

.right{
	right: 10px;
}

.total{
	margin-bottom: 10px;
	border: 0;
}

