.texto_overlay {
	color: white;
    font-size: 1.6rem;
    text-align: center;
    max-width: 80%; /* Ajusta el ancho máximo del texto según tus necesidades */
    max-height: 80%; /* Ajusta la altura máxima del texto según tus necesidades */
    overflow: hidden; /* Recorta el texto si es demasiado largo */
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
	span{
		font-weight: 600;
	}
    a{
        display: inline-block;
        margin-top: 1rem;
        color: #fff;
        font-style: italic;
        font-size: 1.4rem;
    }
}


